import React from 'react';
import './exchange-row.css'

export const ExchangeRow = ({ exchangeKey, exchangeValue }) => {
    return (
        <tr>
            <th>
                1 {`${exchangeKey}`}
            </th>
            <th className="Exchange">
                {`${(100/(exchangeValue * 100)).toFixed(2)} RUB`}
            </th>
            <th>
                {`${((100/(exchangeValue * 100)) + 1.5).toFixed(1)} RUB`}
            </th>
        </tr>
    );
}
