import React, { Component } from 'react';
import { ExchangeRow } from '../exchange-row/exchange-row'
import './exchange-table.css'

export class ExchangeTable extends Component {
    state = {
        data: undefined
    };

    componentDidMount() {
        fetch('https://api.exchangeratesapi.io/latest?base=RUB')
            .then(r => r.json())
            .then(d => this.setState({ data: d.rates }))
    }

    render() {
        console.log('render')
        return (
            <table class="exchangeTable">
                <div className="exchangeTableInner">
                    <tr>
                        <th className="headTd">
                            Валюта
                        </th>
                        <th className="headTd">
                            Реальная стоимость
                        </th>
                        <th className="headTd">
                            Стоимость приобретения
                        </th>
                    </tr>
                    {
                        !this.state.data
                            ? <div>Данные не загружены</div>
                    : Object.keys(this.state.data).map((key) => ( key == "USD" || key == "EUR" || key == "CAD" || key == "GBP"|| key =="CNY")?
                        <ExchangeRow exchangeKey={key} exchangeValue={this.state.data[key]}/>: undefined
                    )
                    }
                </div>
            </table>
        )
    }
}
